import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

const MaterialSelection = ({Material, Type, go, handleMaterial}) => {
	return(
		<Panel id={"materialSelection"} style={{overflow: 'hidden'}}>
			<PanelHeader>
				 <strong></strong>
			</PanelHeader>
			{Type === "" && 
				<Group className="Group">
					<Div>
       	    		    <Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
						<Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
						<Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
						<Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
						<Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
       	    		    <Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
       	    		        level="2"
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>, ,   .     ,   .</Div>}
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>, ,  ,   .</Div>}
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>,     ,     , , , , ,    .</Div>}
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>100%    ,    20 .</Div>}
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>     , , ,   .</Div>}
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>  . , ,  . 100% .   .</Div>}
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			{Type === " " && 
				<Group className="Group">
					<Div>
       	    		    <Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
						<Button className={Material === "-"?
       	    		        "logoButton" : "Button"} 
       	    		        stretched
       	    		        size="xl"
       	    		        level="2"
       	    		        onClick={() => handleMaterial("-")}
       	    		    >-</Button>
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>...</Div>}
       	    	            {Material === "-" && <Div style={{backgroundColor: '#ffffff'}} >- <hr/>...</Div>}
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			{Type === "" && 
				<Group className="Group">
					<Div>
						  .     .
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            <Div style={{backgroundColor: '#ffffff'}} > <hr/>...</Div>
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			{Type === "" && 
				<Group className="Group">
					<Div>
       	    		    <Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
						<Button className={Material === ""?
       	    		        "logoButton" : "Button"} 
       	    		        stretched
       	    		        size="xl"
       	    		        level="2"
       	    		        onClick={() => handleMaterial("")}
       	    		    ></Button>
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>...</Div>}
       	    	            {Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>...</Div>}
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			<FixedLayout vertical="bottom">
				<Div style={{display: 'flex'}}>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onLeft} alt="onLeft"/>} 
						stretched 
						level="2" 
						onClick={go} 
						data-to="typeSelection"
					></Button>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onRight} alt="onRight"/>}
						stretched 
						level="2" 
						onClick={go} 
						data-to="viewSelection"
					></Button>
				</Div>
			</FixedLayout>
		</Panel>
	);
}	

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Type: state.type.Type,
		Material: state.material.Material,
	};
};
  
export default connect(mapStateToProps, actions)(MaterialSelection);